header {
  height: 30px;
  min-width: 100%;
  margin-bottom: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  background: -webkit-repeating-linear-gradient(
    45deg,
    orange,
    orange 15px,
    black 15px,
    black 40px
  );
  background: -o-repeating-linear-gradient(
    45deg,
    orange,
    orange 15px,
    black 15px,
    black 40px
  );
  background: repeating-linear-gradient(
    45deg,
    orange,
    orange 15px,
    black 15px,
    black 40px
  );
}

h2 {
  margin: 0;
}

.nav-btn-wrap {
  position: relative;
  top: 45px;
  margin-right: 15px;
  padding: 6px 20px;
}

.nav-btn {
  height: 33px;
  width: auto;
  border: 0;
  font-size: 24px;
  cursor: pointer;
  background: none;
  color: white;
  font-family: "Cormorant Garamond", serif;
}

.nav-btn:focus {
  outline: 0;
}

.nav-btn:not(:last-child) {
  margin-right: 1vw;
}

@media screen and (max-width: 900px) {
  header {
    margin-bottom: 50px;
  }
}



